import {ComponentProps} from 'react';
import {XIcon} from '@heroicons/react/solid';
import clsx from 'clsx';
import {
  ModalExit,
  ModalOverlay,
  ModalRoot,
  ModalTrigger,
} from '~/components/molecules/modal.client';
import {classVariants} from '~/utils/class-variants';

interface PropsRoot extends ComponentProps<typeof ModalRoot> {}

export function DrawerRoot({children}: PropsRoot) {
  return <ModalRoot>{children}</ModalRoot>;
}

interface PropsTrigger extends ComponentProps<typeof ModalTrigger> {}

export function DrawerTrigger(props: PropsTrigger) {
  return <ModalTrigger {...props} />;
}

interface PropsExit extends ComponentProps<typeof ModalExit> {}

export function DrawerExit(props: PropsExit) {
  return <ModalExit {...props} />;
}

interface PropsOverlay extends ComponentProps<typeof ModalOverlay> {
  position: 'left' | 'right';
}

export function DrawerOverlay({
  position,
  children,
  className,
  ...props
}: PropsOverlay) {
  return (
    <ModalOverlay className="fixed inset-0 z-50 group" {...props}>
      <ModalExit>
        <div className="absolute inset-0 bg-black/50" />
      </ModalExit>
      <div
        className={clsx(
          'absolute top-0 bottom-0 bg-white transition-all',
          classVariants(position, {
            left: 'left-0 -translate-x-10 group-data-is-open:translate-x-0',
            right: 'right-0 translate-x-10 group-data-is-open:translate-x-0',
          }),
          className,
        )}
      >
        <ModalExit
          className={clsx(
            'btn btn-primary btn-icon absolute m-2',
            classVariants(position, {
              left: 'left-full',
              right: 'right-full',
            }),
          )}
        >
          <XIcon className="w-4 h-4" />
        </ModalExit>
        {children}
      </div>
    </ModalOverlay>
  );
}
